
























































































































































































.sideBar {
  width: 80px !important;
  position: fixed;
  top: 0;
  z-index: 99;
  .mytooltip {
    position: relative;
    display: inline-block;

    &:hover {
      cursor: pointer;
    }

    .tooltiptext {
      visibility: hidden;
      width: 120px;
      background-color: #555;
      color: #fff;
      text-align: center;
      border-radius: 6px;
      padding: 5px 0;
      position: absolute;
      z-index: 1;
      top: 2px;
      left: 95px;
      margin-left: -50px;
      opacity: 0;
      transition: opacity 0.3s;

      &::after {
        content: "";
        position: absolute;
        top: 50%;
        right: 100%;
        margin-top: -5px;
        border-width: 5px;
        border-style: solid;
        border-color: transparent #555 transparent transparent;
      }
    }

    &:hover .tooltiptext {
      visibility: visible;
      opacity: 1;
    }
  }
}
@media (max-width: 670px) {
  .sideBar {
      position: absolute;
      left: -60px;
      z-index: 999;
      transition: left 1s;
      &:hover {
        left: 0;
      }
    }
}

